import React, { useEffect, useState } from 'react';
import playLogo from '../assets/playlogo.png';
import { useLocationContext } from '../contexts/Location';
import { useDestinationContext } from '../contexts/Destination';
import useMediaQueries from 'media-queries-in-react';

const Home = () => {
  const { location } = useLocationContext();
  const { destination } = useDestinationContext();
  const [className, setClassName] = useState();
  const [showContent, setShowContent] = useState(false);
  const mediaQueries = useMediaQueries({
    screen: 'screen and (min-width: 750px)',
  });
  useEffect(() => {
    setShowContent(false);
    const timer = setTimeout(() => {
      setShowContent(true);
    }, 1000);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    if (
      destination !== null &&
      destination !== 'home' &&
      (destination === 'projet' || destination === 'skills')
    )
      setClassName('leave-left');
    else if (location !== 'welcome' && (location === 'projet' || 'skills')) {
      setClassName('incoming-left');
    } else if (location === 'welcome') {
      setClassName('display-middle');
    }
  }, [location, destination]);

  return (
    <div className='home-container'>
      <div className={`${className}-auto-text-container`}>
        {mediaQueries.screen ? (
          <>
            <h1 id='title-auto-text'>Je suis Nicolas Berthollet</h1>
            <span id='auto-text'></span>
          </>
        ) : (
          <>
            <h1 id='title-homepage-responsive'>Je suis Nicolas Berthollet</h1>
          </>
        )}
      </div>
      {showContent ? (
        <>
          <section className='homepage-content'>
            <div className={`${className}-presentation-container`}>
              <div className='projet-box5'></div>
              <div className='presentation-content'>
                <h3>prsentation</h3>
                <p className='text'>
                  Aprs une exprience russie en restauration, j'ai dcid
                  d'apprendre le dveloppement web et plus prcisement le mtier
                  de dveloppeur web frontend. <br /> en formation  la{' '}
                  <span className='text-link'>
                    <a href='https://www.wildcodeschool.com/fr-FR.wild'>
                      wild code school
                    </a>
                  </span>{' '}
                  j'apprends  dvelopper le frontend et le backend d'une
                  application web avec react / nodejs / expressjs / mysql.{' '}
                  <br /> je suis  la recherche d'un stage en tant que
                  dveloppeur frontend d'une dure de 4 mois du 3 Juillet 2023
                  au 7 novembre 2023 pour complter mon cursus et mettre mes
                  nouvelles comptences en action. <br /> si vous le souhaitez
                  voici{' '}
                  <span className='text-link'>
                    <a href='./img/CV_Nicolas_Berthollet.pdf' download>
                      mon cv
                    </a>
                  </span>
                </p>
              </div>
            </div>
            <div className={`${className}-home-project-container`}>
              <div className='projet-box5'></div>
              <div className='home-projet-content'>
                <h3>mon dernier projet</h3>
                <div className='projet-link-container'>
                  <a
                    href='https://travelers-gig.netlify.app/'
                    className='projet-link'
                    target='blank'
                  >
                    <img
                      src={playLogo}
                      alt='Bouton pour accder au projet'
                      className='home-play-logo'
                    />
                  </a>
                </div>
                <h3>TRAVELERS-GIG</h3>
                <p className='text'>
                  <span className='underline'> Projet Hackaton : </span> <br />
                  Cration d'un site qui rpertorie les concerts dans le monde
                  grce  une recherche par Artiste
                </p>
              </div>
            </div>
          </section>
        </>
      ) : null}
    </div>
  );
};

export default Home;
